<template>
  <div @click="GoArticleDetail">
    <el-card class="box-card" style="width: 64%; margin-left: 210px">
      <div slot="header" class="clearfix">
        <span>{{articleData.title}}</span>
        <el-tag type="warning" v-if="!articleData.approved"></el-tag>
      </div>
      <el-row :gutter="10">
        <el-col :span="24">
          <el-image
            :src="articleData.cover"
            style="width: 60%; margin-left: 36px"
          >
            <div slot="placeholder" class="image-slot">
              <span class="dot">...</span>
            </div>
            <div slot="error" class="image-slot">
              <i class="el-icon-picture-outline"></i>
            </div>
          </el-image>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="24">
          <p>{{articleData.description}}</p>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="2">
          <el-avatar :src="articleData.author.avatar" size="small" style="margin-top: 14px"></el-avatar>
        </el-col>
        <el-col :span="6">
          <p>{{articleData.author.userName}}</p>
        </el-col>
        <el-col :span="4">
        <p>{{articleData.lastModified | transformTime}}</p>
      </el-col>
        <el-col :span="7" :offset="5" style="display: flex; justify-content: space-between; ">
          <el-badge :value="articleData.likes" class="item">
            <el-button size="small"></el-button>
          </el-badge>
          <el-badge :value="articleData.views" class="item" type="primary">
            <el-button size="small"></el-button>
          </el-badge>
          <el-badge :value="articleData.comment.length" class="item" type="warning">
            <el-button size="small"></el-button>
          </el-badge>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import AuthorService from "@/service/AuthorService";
import defaultConfig from "@/config/config.default";
export default {
  name: "Global-Article",
  props: {
    articleData: {
      type: Object,
      default() {
        return {
          title: '',
          tags: [],
          description: '',
          cover: '',
          comment: [],
          author: {},
          lastModified: new Date(),
          views: 0,
          likes: 0,
          blogId: 1, // id 
          approved: true
        }
      }
    }
  },
  filters: {
    transformTime(time) {
      let blogCreateTime = new Date(time); // 
      let currentTime = new Date(); // 
      let offsetTime = currentTime - blogCreateTime; //  
      let offsetST = offsetTime / 1000; // s
      let offsetMT = offsetST / 60; // min
      let offsetHT = offsetMT / 60; //h
      let offsetDT = offsetHT / 24; //day
      if (offsetDT >= 365) {
        return `${Math.floor(offsetDT / 365)}`;
      } else if (offsetDT >= 30 && offsetDT < 365) {
        return `${Math.floor(offsetDT / 30)}`;
      } else if (offsetDT >= 7 && offsetDT < 30) {
        return `${Math.floor(offsetDT / 7)}`;
      } else if (offsetDT >= 1 && offsetDT < 7) {
        return `${Math.floor(offsetDT)}`;
      } else if (offsetHT >= 1) {
        return `${Math.floor(offsetHT)}`;
      } else if (offsetMT >= 1) {
        return `${Math.floor(offsetMT)}`;
      } else if (offsetST >= 1) {
        return `${Math.floor(offsetST)}`;
      }
    }
  },
  methods: {
    GoArticleDetail() {
      AuthorService.checkPermission().then(rs=>{
        if (rs.data.status === 200) {
          this.$router.push(`/article/${this.articleData.blogId}`);
        } else {
          window.location.replace(`${defaultConfig.hostname}/login.html`);
        }
      })
    }
  }
}
</script>

